* {
  font-family: sans-serif;
  text-align: center;
}

html,
body,
div,
span,
applet,
object,
h1,
video {
  margin: 0;
  padding: 0;
  border: 0;
}

.all {
  background-image: url("https://drive.google.com/uc?export=view&id=1wfPHsG-xqMfhL-foTbusAorFy291FTN7");
  background-size: cover;
}

.back {
  background: rgba(255, 255, 255, 0.3);
}

.slider {
  padding-left: 40%;
  padding-top: 40px;
}

h1 {
  font-size: 48px;
}

h2 {
  font-size: 28px;
}

h3 {
  font-size: 24px;
  margin-bottom: 0;
  padding-bottom: 0;
}

.shousai {
  font-size: 18px;
}
